* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito Sans", "sans-serif";
  color: white;
}
.App {
  margin: 0 32px;
}
#root {
  background-color: #292524;
}

/*LAYOUT*/

.header {
  text-align: center;
}
ul {
  list-style: circle;
}
a {
  text-decoration: none;
}
.name {
  font-size: 42px;
}
.contact-info {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  margin: 16px 0;
}
.section-title {
  font-size: 28px;
  margin-top: 5px;
  margin-bottom: 10px;
}
.sections {
  margin: 10px 0;
  background-color: #44403c;
  border-radius: 25px;
  padding: 12px 24px;
}

/*BREAKPOINT AND MEDIA QUERIES*/

@media (max-width: 1000px) {
  .contact-info {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 2px;
  }
}

* .duration {
  float: right;
}
